  max-width: 70vw;
}

.row { /* IE10 */
  display: flex;
  align-content: center;
  margin: 2vw 0vw 5vw 0vw;
  justify-content: center; /* IE10 */
  flex-wrap: wrap;
  padding: 0 4px;
}

/* Create four equal columns that sits next to each other */
.column { /* IE10 */
  flex: 25% 1;
  max-width: 25%;
  padding: 0 4px;
}

.column img {
  margin-top: 8px;
  vertical-align: middle;
  width: 100%;
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 500px) {
  .barbers {
    margin: 3vw 0vw 0vw 0vw;
  }

  .barber-info {
    width: 50vw;
  }

  .barber-img {
    max-width: 30vw;
    margin-right: 3vw;
  }

  .barber-name {
    font-size: 5vw;
    margin-bottom: 0.5vw;
  }

  .barber-details {
    font-size: 2.5vw;
  }

  .company-history {
    margin-top: 6vw;
  }

  .company-history-h2 {
    font-size: 6vw;
  }
  .company-history-p {
    font-size: 2.5vw;
  }

  .column {
    flex: 50% 1;
    max-width: 50%;
  }
  .about-gallery {
    margin: 0vw 2vw 0vw 2vw;
  }
}

.contact-form-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
  background-color: rgba(0, 0, 0, 0.89);
  width: 50vw;
  height: 650px;
  margin: auto;
  margin-top: 8vh;
  margin-bottom: 8vh;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  /* padding: 70px 100px; */
  padding: 70px 0px;
  color: white;
  overflow: hidden;
  font-family: "Noto Sans", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

